import axios from "axios";

export class booksAPI {

    //     
    #BASE_URL = 'https://books-backend.p.goit.global/books/';

    // 
    getBookById(book_Id) {console.log(`${this.#BASE_URL}${book_Id}`); return axios.get(`${this.#BASE_URL}${book_Id}`);}
    getTopBooks() {return axios.get(`${this.#BASE_URL}top-books`);}
    getCategoryList() {return axios.get(`${this.#BASE_URL}category-list`);}
    getBooksByCategory(category) {
        console.log(`${this.#BASE_URL}category?category=${category}`);
        return axios.get(`${this.#BASE_URL}category?category=${category}`)};
}
